@import 'sass/mixin';
@import 'sass/nullstyle';
@import 'sass/bem';
@import 'sass/myBootstrap';


body {

  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #E7F6FF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  margin: 0 auto;
  max-width: 1380px;
}


.frame {
  max-width: 1380px;
  display: block;
  @include adaptiv-value(margin, 85, 10);
  background-color: #fff;
  @include adaptiv-value(padding, 45, 10);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;

  @media (max-width: 600px) {
    border-radius: 10px;
  }
}


.header {
  border-bottom: 1px solid #EAEAEA;
  @include adaptiv-value(padding-bottom, 44, 10);

}

.header__nav {
  @include adaptiv-value(gap, 30, 10);

}

.header__nav-basket {
  img {
    animation: basket 4s linear infinite normal 0s forwards;
    margin-right: 40px;
  }
}

@keyframes basket {
  0% {}

  100% {
    transform: translate(30px, 0) rotate(30deg);
  }
}



.sneakers__card-img {
	img {
	  margin-bottom: 12px;
    @media (max-width: 480px){
      margin-bottom: 7px;
    }
	}
 }
 
 .sneakers__card {
	position: relative;
	max-width: 260px;
	font-size: 14px;
	line-height: 130%;
	border: 1px solid #F3F3F3;
	@include adaptiv-value(border-radius,38,20);
	@include adaptiv-value(padding, 20, 5);
	@include adaptiv-value(padding-top, 20, 10);
	@include adaptiv-value(padding-bottom, 20, 10);
	transition: all 0.3s linear;
 
	label {
	  font-weight: 500;
	  font-size: 11px;
	  line-height: 13px;
	  text-transform: uppercase;
	  color: #BDBDBD;
	  margin-bottom: 2px;
	}
 
	&:hover {
	  transition: all 0.3s linear;
	  transform: translateY(-7px);
	  box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
	}
 




 }



 
.sneakers__card-title {
	margin-bottom: 15px;
 }
 
 
 .sneakers__card-plus {
	display: inline-block;
  border-radius: 8px;
 }
 .sneakers__net-card {
	height: 100%;
 }


 .sneakers__net-card-wr {
	margin: 0 auto;
 }

 .card__favorite {
	position: absolute;
	@include adaptiv-value(top,30,10);
  @include adaptiv-value(left,30,10);
  
	
 }



.header__logo {
  gap: 15px;
}

.header__logo-text {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }

}

.basket-price {
  display: none;

  @media (min-width: 576px) {
    display: block;
    color: #5C5C5C;
    @include adaptiv-value(font-size, 14, 12);
    line-height: 1;
  }
}

.header__logo-text {
  h3 {
    font-weight: 700;
    @include adaptiv-value(font-size, 20, 16);
    line-height: 130%;
  }

  p {
    color: #9D9D9D;
    font-size: 14px;
  }
}

h1 {
  @include adaptiv-value(font-size, 32, 17);
  font-weight: 700;
  line-height: 130%;
}

.sneakers__title {
  @media (max-width: 767px){
    flex-direction: column;
    align-items: flex-start;
  }
  
  @include adaptiv-value(margin-top, 40, 20);
  @include adaptiv-value(margin-bottom, 40, 20);
  gap: 20px;

 

  input {
    border: 1px solid #F3F3F3;
    border-radius: 10px;
    @include adaptiv-value(width, 250, 170);
    @include adaptiv-value(height, 45, 35);
    @include adaptiv-value(padding-left, 46, 30);
    transition: all 0.3s linear;

    &::placeholder {
      font-family: inherit;
      color: #C4C4C4;
      font-weight: 400;
      font-size: 14px;
    }

    &:focus {
      transition: all 0.3s linear;
      border: 1px solid skyblue;
    }
  }

}

.search__img {
  position: absolute;

  @include adaptiv-value(padding, 15, 9);

  top: 0px;
  left: 0px;
  cursor: pointer;
}



.header__nav-follovers {
  svg {
    path {
      animation: favorite 5s linear infinite alternate 0s forwards;
    }
  }
}


@keyframes favorite {
  0% {
    fill: #9B9B9B;

  }

  50% {
    fill: red;

  }

  100% {
    fill: #9B9B9B;
  }
}

.sneakers__net {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  @include adaptiv-value(gap,30,10);
  
  @media (max-width: 992px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr 1fr;
  }

  
}



.sneakers__card{
  height: 100%;
 
}

.drawer__btn{
  margin-top: 20px;
}















//

.drawer__wr {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.drawer {
  position: absolute;
  right: 0;
  top: 0;
  background-color: #fff;
  width: 390px;
  @include adaptiv-value(padding, 30, 20);

  @media (max-width: 420px) {
    width: 100%;
  }

  height: 100%;
  max-height: 100%;
}


.drawer__title {
  @include adaptiv-value(font-size, 24, 18);
  margin-bottom: 30px;

}


.drawer__cards {
  width: 100%;
  overflow: auto;
}




.drawer__cards-item {
  border: 1px solid #F3F3F3;
  border-radius: 20px;
  padding: 17px;
  gap: 10px;
  margin-bottom: 20px;
  overflow: hidden;
}


.drawer__item-title {
  font-size: 14px;

  p {
    margin-bottom: 8px;

  }

}


.dashed {
  display: block;
  border-bottom: 2px dashed #DFDFDF;
}

.drawer__btn {
  width: 100%;
  font-size: 16px;
  gap: 20px;
}


.drawer__btn-net {
  gap: 10px;
}

.drawer__btn-btn {
  padding-left: 25%;
  @include adaptiv-value(gap,40,20);
  padding-top: 18px;
  padding-bottom: 18px;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  width: 100%;
  background-color: #9DD458;
  border-radius: 18px;
  transition: all 0.3s linear;
  @media(any-hover: hover){&:hover{
   img{
    transition: all 0.3s linear;
    transform: translateX(10px);
   }
  }}
  
}
.sneakers__card-img{
  align-self: center;
}


.search__empty{
position: absolute;
top: 50%;
right: 5px;
@include adaptiv-value(width,60,40);
@include adaptiv-value(height,60,40);
transform: translate(0,-50%);
z-index: 2;
}
.re{
  input{
    padding-right: 10px;
  }
}



.drawer__empty{
  width: 100%;
  height: 100%;
  line-height: 150%;
  img{
    margin-bottom: 20px;
  }
  h3{
    font-weight: 600;
    @include adaptiv-value(font-size,22,18);
  }
  p{
    opacity: 0.4;
    margin-top: 10px;
  }
}


.favirite__title{
@include adaptiv-value(margin-top,40,20);
@include adaptiv-value(margin-bottom,40,20);
}


.favorites__empty{
  height: 50vh;
  @media (max-width: 767px){
    height: 80vh;
  }
  .favorites__empty-img{
    margin-bottom: 20px;
    @include adaptiv-value(width,70,40);
  }
  h3{
    margin-bottom: 10px;
  }
  p{
    opacity: 0.4;
    padding: 0 5px;
  }
}

.favorites__btn{
  color: #fff;
 @include adaptiv-value( border-radius,18,10);
 
  background: #9DD458;
  @include adaptiv-value(margin-top,70,40);
  @include adaptiv-value(padding-top,17,10);
  @include adaptiv-value(padding-bottom,17,10);
  @include adaptiv-value(padding-left,30,15);
  @include adaptiv-value(padding-right,30,15);
gap: 20px;
  img{
    width: 20px;
    height: 20px;
  }
  
}